.App {
  text-align: center;
}

* {
  font-size: 20px;
  font-family:Arial, Helvetica, sans-serif;
}

body {
  background-color: rgb(231, 231, 231);
  text-align: left;
  margin: 0;
}

h1,h2 {
  
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 25px;
  max-width: fit-content;
  text-shadow: 1px 1px 1px black;
}

h1 {
  font-size: 64px;
  text-align: left;
}

h2 {
  font-size: 24px;
  text-align: right;
  max-height: fit-content;
  margin-top: 38px;
}

#titleContainer {
  background-color: rgb(202, 202, 202);
  display: flex;
}

#gradientUnderTitle {
  background-image: linear-gradient(rgb(202, 202, 202),rgb(231, 231, 231));
  padding: 2px;
}